body {
  padding: 0;
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-size: 15px;
  width: 100%;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Raleway", sans-serif;
}

h2 {
  color: #212529;
  font-weight: 600;
  font-size: 33px;
  margin-bottom: 30px;
  margin-top: 15px;
  font-family: Merriweather, serif;
}

h3 {
  font-size: 25px;
  color: #212529;
  font-weight: 500;
  margin-bottom: 15px;
  font-family: Merriweather, serif;
}

h4 {
  font-size: 20px;
  font-weight: 600;
  color: #212529;
  position: relative;
  margin-bottom: 18px;
  font-family: Merriweather, serif;
}

p {
  color: #57647C;
  font-family: "Raleway", sans-serif;
  font-size: 18px;
}

#header {
  background: #fff;
  transition: all 0.5s;
  z-index: 997;
  padding: 20px 0;
}

#header.header-scrolled {
  padding: 12px 0;
  box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
}

#header .logo {
  font-size: 30px;
  margin: 0;
  padding: 0;
  font-weight: 600;
}

#header .logo img {
  max-height: 75px;
}

.fa {
  font-size: 25px;
}

.dropdown {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 18px;
  font-weight: 500;
  border: none;
  outline: none;
  color: black;
  padding: 5px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.navbar a:hover,
.dropdown:hover .dropbtn {
  background-color: red;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-content a:hover {
  background-color: #ddd;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.section-title {
  text-align: center;
}

.navbar {
  padding: 0;
}

.navbar ul {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
  align-items: center;
}

.button1 {
  margin: 5px;
  padding: 6px 30px;
  color: #ddd;
  background: #EE2B33;
  border-color: #EE2B33;
  text-transform: capitalize;
  font-size: 16px;
  font-weight: 700;
  display: block;
}

.align-items-center {
  align-items: center !important;
}

.ctp-banner-form {
  margin-left: auto;
}

.ctp-banner-form .form-header {
  padding: 1px;
  border-radius: 15px;
